
body{
    background: #CBF4FA;
}
header{
    background: url('./../assets/img/title.png') left top no-repeat;
    background-size:100% 100%;
    width:100%;
    height:12rem;
    position:relative;
    top:0;
    z-index: 4;
}
section{
    background: url('./../assets/img/bg.png') left top no-repeat;
    background-size:100% 100%;
    width:100%;
    height:26rem;
    position:relative;
    top:0;
    z-index:3;
    cursor: pointer;
}
nav{
    width: 8rem;
    height: 1.5rem;
    line-height: 1.5rem;
    background: url('./../assets/img/span.png') left top no-repeat;
    font-size: 0.9rem;
    background-size: 100% 100%;
    position: absolute;
    left: calc(50% - 4rem);
    bottom: 1rem;
    color: #ff8d24;
    font-weight: bold;
    word-spacing:.1rem;
}
nav p{
    text-indent: 1.6em;
}
nav span{
    color:#ff0101;
}
article{
    font-size:.9rem;
    position:absolute;
    bottom:.7rem;
    right:.5rem;
    color:#fff;
}
aside{
    background: url('./../assets/img/gift.png') left top no-repeat;
    font-size: 0.9rem;
    background-size: 100%;
    width:6rem;
    height:23rem;
    position:absolute;
    left:calc(50% - 3rem);
    top:-.5rem;
    opacity: 1;
    cursor: pointer;
    /*animation:sroll5 2s linear 1;*/
}
.finger{
    background: url('./../assets/img/finger.png') left top no-repeat;
    font-size: 0.9rem;
    background-size: 100%;
    width:6rem;
    height:2rem;
    position:absolute;
    left:5rem;
    top:3rem;
    -webkit-animation:quickroll 1s infinite;
            animation:quickroll 1s infinite;
}
@-webkit-keyframes quickroll {
0% {
    -webkit-transform:translateX(0);
            transform:translateX(0)
}
100% {
    -webkit-transform:translateX(9rem);
            transform:translateX(9rem);
}
}
@keyframes quickroll {
0% {
    -webkit-transform:translateX(0);
            transform:translateX(0)
}
100% {
    -webkit-transform:translateX(9rem);
            transform:translateX(9rem);
}
}
@-webkit-keyframes slowroll {
0% {
    -webkit-transform:translateY(0);
            transform:translateY(0);
    opacity:1;
}
100% {
    -webkit-transform:translateY(16rem);
            transform:translateY(16rem);
    opacity:0;
}
}
@keyframes slowroll {
0% {
    -webkit-transform:translateY(0);
            transform:translateY(0);
    opacity:1;
}
100% {
    -webkit-transform:translateY(16rem);
            transform:translateY(16rem);
    opacity:0;
}
}
@-webkit-keyframes sroll1 {
0% {
    -webkit-transform:rotate(0);
            transform:rotate(0);
    -webkit-transform-origin:center top;
            transform-origin:center top;
}
25%{
    -webkit-transform:rotate(1deg);
            transform:rotate(1deg);
    -webkit-transform-origin:center top;
            transform-origin:center top;
}
50%{
    -webkit-transform:rotate(0);
            transform:rotate(0);
    -webkit-transform-origin:center top;
            transform-origin:center top;
}
75%{
    -webkit-transform:rotate(-1deg);
            transform:rotate(-1deg);
    -webkit-transform-origin:center top;
            transform-origin:center top;
}
100% {
    -webkit-transform:rotate(0);
            transform:rotate(0);
    -webkit-transform-origin:center top;
            transform-origin:center top;
}
}
