footer{
    position:fixed;
    width: 100%;
    height:100%;
    top: 0;
    z-index:10000;
}
.shadow{
    position:absolute;
    background:#000;
    opacity: .7;
    width:100%;
    height:100%;
    top: 0;
    z-index: 1;
}
.child{
    position:relative;
    z-index:2;
}
.rule{
    width: 100%;
    height: 35rem;
    position: absolute;
    z-index: 2;
    top:0;
    background: url('./../assets/img/rule.png') left top no-repeat;
    background-size: 100%;
}
.close{
    background: url('./../assets/img/close.png') left top no-repeat;
    background-size: 100%;
    width: 2.2rem;
    height: 5.2rem;
    position: absolute;
    z-index: 100;
}
.win{
    position:relative;
    height:29rem;
}
.win .img{
    width: 100%;
    height:100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
}
.win .close{
    top: 8.6rem;
    right:4rem;
}
.win p{
    position: absolute;
    top: 25.3rem;
    left: calc(50% - 5rem);
    width:10rem;
    height:3rem;
    border:none;
    z-index:10;
}
.win p img{
    width:100%;
}
p.copyright{
    color:#8C3223;
    background:#9DD84A;
}
.share{
    color: #fff;
    text-align: center;
    position: fixed;
    z-index: 5;
    top:0;
    width: 100%;
    height:100%;
}
.shareBg{
    position: relative;
    z-index: 6;
}
.shareShadow{
    background: url('./../assets/img/arrow.png') center center no-repeat;
    background-size: 100% 100%;
    width: 100%;
    height: 25rem;
    position: relative;
    top:0;
}
.text{
    position: relative;
}
.text span{
    font-size: 1.3rem;
}



